.button {
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
  }
  
  .buttonup {
    background-color: white; 
    color: black; 
    border: 2px solid #04AA6D;
  }
  
  .buttonup:hover {
    background-color: #04AA6D;
    color: white;
  }
  .dropbtn {
    background-color: white; 
    color: black; 
    border: 2px solid #04AA6D;
  }
  
  .dropbtn:hover {
    background-color: #04AA6D;
    color: white;
  }
  .buttondown {
    background-color: white; 
    color: black; 
    border: 2px solid #008CBA;
  }
  
  .buttondown:hover {
    background-color: #008CBA;
    color: white;
  }
  /* 设置下拉按钮的样式 */
/* .dropbtn {
