.link-budget {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .introduction {
    margin-bottom: 30px;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
  }
  
  table caption {
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  th, td {
    padding: 10px;
    border: 1px solid #ddd;
    text-align: center;
  }
  
  .calculator {
    margin: 30px 0;
  }
  
  .dropdown {
    position: relative;
    display: inline-block;
    margin: 10px;
  }
  
  .dropbtn {
    background-color: #4CAF50;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;
    cursor: pointer;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    padding: 12px;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
  
  input[type="number"] {
    width: 100px;
    padding: 5px;
    margin: 5px 0;
  }
  
  .results {
    margin-top: 30px;
  }
  
  .propagation-model {
    margin-top: 30px;
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 5px;
  }